// --------------------------------------------------
// Project overall color-swatches:
// --------------------------------------------------

$swatchBlue         	  : #008CBE;
$swatchBlueDark     	  : #322882;
$swatchPurple       	  : #821E7D;
$swatchOrange        	  : #E56C00;
$swatchGreen         	  : #7DB43C;
$swatchGreyDark     	  : #525151;
$swatchGreyMediumDark   : #929292;
$swatchGrey		        	: #D7D7D7;
$swatchGreyLight    	  : #EEE;
$swatchGreyMediumLight  : #f7f7f7;
$swatchGreyLighter      : #FBFBFB;
$swatchBeige            : #F9F6F6;

// --------------------------------------------------
// Project-specific
// --------------------------------------------------

$colorPrimary   	: $swatchPurple;
$colorSecondary 	: $swatchBlue;

$colorAttention 	: $swatchOrange;
$colorAttentionAlt 	: $swatchGreen;

$colorBackgroundAlt : $swatchBeige;

$colorWhite 		: #fff;
$colorBlack 		: $swatchGreyDark;
$colorGreyDark  	: $swatchGreyDark;
$colorGrey      	: $swatchGrey;
$colorGreyLight 	: $swatchGreyLight;
$colorGreyLighter   : $swatchGreyLighter;

$border-color		: $colorGrey;


// --------------------------------------------------
// Bootstrap config:
// --------------------------------------------------
// Variables
// --------------------------------------------------

//== Colors
//
//## Gray and brand colors for use across Bootstrap.
$gray-darker:            lighten(#000, 13.5%); // #222
$gray-dark:              $swatchGreyDark;
$gray:                   lighten(#000, 33.5%); // #555
$gray-light:             $swatchGreyLight;
$gray-lighter:           lighten(#000, 93.5%); // #eee

$brand-primary:         $colorPrimary;

$brand-success:         $colorAttentionAlt;
$brand-info:            #5bc0de;
$brand-warning:         $colorAttention;
$brand-danger:          #d9534f;

//== Scaffolding
//
// ## Settings for some of the most global styles.

//** Background color for `<body>`.
$body-bg:               $colorWhite;
//** Global text color on `<body>`.
$text-color:            $swatchGreyDark;

//** Global textual link color.
$link-color:            $colorSecondary;
//** Link hover color set via `darken()` function.
$link-hover-color:      $link-color;


//== Typography

$font-family-base:        "Proxima Nova Regular", Verdana, Arial !default;
$font-size-base:          16px !default;
$font-size-footer:        14px !default;

$font-size-extra-small:   ceil($font-size-base * 0.75) !default; // 12px
$font-size-small:         ceil($font-size-base * 0.85) !default; // 14px
$font-size-medium:        ceil($font-size-base * 1.1) !default;  // 18px
$font-size-large:         floor($font-size-base * 1.3) !default; // 20px
$font-size-extra-large:   floor($font-size-base * 1.5) !default; // 24px

$font-size-h1:            floor($font-size-base * 2.4) !default; // 38px
$font-size-h2:            floor($font-size-base * 1.9) !default; // 30px
$font-size-h3:            $font-size-extra-large !default;       // 24px
$font-size-h4:            $font-size-medium !default;            // 18px
$font-size-h5:            $font-size-base !default;              // 16px
$font-size-h6:            floor($font-size-base * 0.9) !default; // 14px

$font-weight-lighter:     100 !default;
$font-weight-base:        200 !default;
$font-weight-light:       300 !default;
$font-weight-regular:     400 !default;
$font-weight-medium:      500 !default;
$font-weight-bold:        600 !default;

$headings-font-family:    "Proxima Nova Light", Verdana, Arial !default;
$headings-font-weight:    $font-weight-light;
$headings-line-height:    1.2;
$headings-color:          $colorPrimary;

//** Unit-less `line-height` for use in components like buttons.
$line-height-base:        1.428571429 !default; // 20/14
//** Computed "line-height" (`font-size` * `line-height`) for use with `margin`, `padding`, etc.
$line-height-computed:    20px !default; // ~20px

//-- Iconography
$icon-liander-font-name:    "icons-liander-nl";

//== Spacing
$spacing-base: 20px !default;

//== Components
//
//## Define common padding and border radius sizes and more. Values based on 14px text and 1.428 line-height (~20px to start).

$padding-base-vertical:     6px;
$padding-base-horizontal:   12px;

$padding-large-vertical:    16px;
$padding-large-horizontal:  16px;

$padding-small-vertical:    5px;
$padding-small-horizontal:  10px;

$padding-xs-vertical:       1px;
$padding-xs-horizontal:     5px;

$line-height-large:         1.33;
$line-height-small:         1.5;

$border-radius-base:        3px;
$border-radius-large:       $border-radius-base * 2;
$border-radius-small:       6px;

//** Global color for active items (e.g., navs or dropdowns).
$component-active-color:    $colorWhite;
//** Global background color for active items (e.g., navs or dropdowns).
$component-active-bg:       $brand-primary;

//** Width of the `border` for generating carets that indicator dropdowns.
$caret-width-base:          4px;
//** Carets increase slightly in size for larger components.
$caret-width-large:         5px;


//== Tables
$table-cell-padding:    12px;

//== Buttons
//
//## For each of Bootstrap's buttons, define text, background and border color.
$btn-font-weight:                normal;

$btn-default-color:              $colorGreyDark;
$btn-default-bg:                 $colorWhite;
$btn-default-border:             $colorGrey;

$btn-primary-color:              $colorWhite;
$btn-primary-bg:                 $colorAttention;
$btn-primary-border:             $btn-primary-bg;

$btn-success-color:              $colorWhite;
$btn-success-bg:                 $brand-success;
$btn-success-border:             $btn-success-bg;

$btn-info-color:                 $colorWhite;
$btn-info-bg:                    $brand-info;
$btn-info-border:                $btn-info-bg;

$btn-warning-color:              $colorWhite;
$btn-warning-bg:                 $brand-warning;
$btn-warning-border:             $btn-warning-bg;

$btn-danger-color:               $colorWhite;
$btn-danger-bg:                  $brand-danger;
$btn-danger-border:              $btn-danger-bg;

$btn-link-disabled-color:        $gray-light;

//== Type
//
//##

//** Text muted color
$text-muted:                  $gray-light;
//** Abbreviations and acronyms border color
$abbr-border-color:           $gray-light;
//** Headings small color
$headings-small-color:        lighten($text-color, 20%);
//** Blockquote small color
$blockquote-small-color:      $gray-light;
//** Blockquote font size
$blockquote-font-size:        ($font-size-base * 1.25);
//** Blockquote border color
$blockquote-border-color:     $gray-lighter;
//** Page header border color
$page-header-border-color:    $gray-lighter;

//== Media queries breakpoints
//
//## Define the breakpoints at which your layout will change, adapting to different screen sizes.

// Extra small screen / phone
$screen-xs:                  480px;
$screen-xs-min:              $screen-xs;
$screen-phone:               $screen-xs-min;

// Extra between small and extra small screen (very small)
$screen-vs:                  600px;
$screen-vs-min:              $screen-vs;
$screen-largephone:          $screen-vs-min;


// Small screen / tablet
$screen-sm:                  768px;
$screen-sm-min:              $screen-sm;
$screen-tablet:              $screen-sm-min;

// Medium screen / desktop
$screen-md:                  992px;
$screen-md-min:              $screen-md;
$screen-desktop:             $screen-md-min;

// Large screen / wide desktop
$screen-lg:                  1200px;
$screen-lg-min:              $screen-lg;
$screen-lg-desktop:          $screen-lg-min;

// So media queries don't overlap when required, provide a maximum
$screen-xs-max:              ($screen-sm-min - 1);
$screen-vs-max:              ($screen-sm-min - 1);
$screen-sm-max:              ($screen-md-min - 1);
$screen-md-max:              ($screen-lg-min - 1);

$device-max-width:  1280px; // A maximum width to target devices

//== Grid system
//
//## Define your custom responsive grid.

//** Number of columns in the grid.
$grid-columns:              12;
//** Padding between columns. Gets divided in half for the left and right.
$grid-gutter-width:         20px;
// Navbar collapse
//** Point at which the navbar becomes uncollapsed.
$grid-float-breakpoint:     $screen-sm-min;
//** Point at which the navbar begins collapsing.
$grid-float-breakpoint-max: ($grid-float-breakpoint - 1);

//== Container sizes
//
//## Define the maximum width of `.container` for different screen sizes.

// Small screen / tablet
$container-tablet:             ((720px + $grid-gutter-width));
//** For `$screen-sm-min` and up.
$container-sm:                 $container-tablet;
$container-vs:                 600px;

// Medium screen / desktop
$container-desktop:            ((940px + $grid-gutter-width));
//** For `$screen-md-min` and up.
$container-md:                 $container-desktop;

// Large screen / wide desktop
$container-large-desktop:      ((1140px + $grid-gutter-width));
//** For `$screen-lg-min` and up.
$container-lg:                 $container-large-desktop;

//== Forms

//** `<input>` background color
$input-bg:                       $colorWhite;
//** `<input disabled>` background color
$input-bg-disabled:              $gray-lighter;

//** Text color for `<input>`s
$input-color:                    $colorGreyDark;
//** `<input>` border color
$input-border:                   $colorGrey;
//** `<input>` border radius
$input-border-radius:            3px;
//** Border color for inputs on focus
$input-border-focus:             $colorSecondary;

//** Placeholder text color
$input-color-placeholder:        #999;

//** Default `.form-control` height
$input-height-base:              ($line-height-computed + ($padding-base-vertical * 2) + 2) !default;
//** Large `.form-control` height
$input-height-large:             (ceil($font-size-large * $line-height-large) + ($padding-large-vertical * 2) + 2) !default;
//** Small `.form-control` height
$input-height-small:             (floor($font-size-small * $line-height-small) + ($padding-small-vertical * 2) + 2) !default;

$legend-color:                   $colorGreyDark;
$legend-border-color:            $colorGreyLight;

//** Background color for textual input addons
$input-group-addon-bg:           $colorWhite;
//** Border color for textual input addons
$input-group-addon-border-color: $colorGrey;

$state-danger-border: $swatchOrange;

//== Navs
//== Tabs

$tab-content-border-radius:               $border-radius-large;

$tab-pane-body-padding:                   $spacing-base;

$nav-tabs-primary-link-border-color:      $swatchPurple;
$nav-tabs-primary-color:                  $swatchPurple;
$nav-tabs-primary-active-color:           #fff;
$nav-tabs-primary-background-color:       $swatchPurple;
$tab-content-primary-color:               #fff;
$tab-content-primary-background-color:    $swatchPurple;

$nav-tabs-secondary-link-border-color:    $swatchBlue;
$nav-tabs-secondary-color:                $swatchBlue;
$nav-tabs-secondary-active-color:         #fff;
$nav-tabs-secondary-background-color:     $swatchBlue;
$tab-content-secondary-color:             #fff;
$tab-content-secondary-background-color:  $swatchBlue;

//== Navbar

// Basics of a navbar
$navbar-height:                    70px !default;
$navbar-margin-bottom:             0;
$navbar-border-radius:             0;
$navbar-padding-horizontal:        floor(($grid-gutter-width / 2)) !default;
$navbar-padding-vertical:          (($navbar-height - $line-height-computed) / 2) !default;
$navbar-collapse-max-height:       none;

$navbar-default-color:             $colorGrey;
$navbar-default-bg:                $colorWhite;
$navbar-default-border:            transparent;

// Navbar links
$navbar-default-link-color:                $colorGreyDark;
$navbar-default-link-hover-color:          $colorSecondary;
$navbar-default-link-hover-bg:             transparent !default;
$navbar-default-link-active-color:         $colorSecondary;
$navbar-default-link-active-bg:            transparent;
$navbar-default-link-disabled-color:       #ccc !default;
$navbar-default-link-disabled-bg:          transparent !default;

// Navbar toggle
$navbar-default-toggle-hover-color:       $colorSecondary;
$navbar-default-toggle-hover-bg:           transparent;
$navbar-default-toggle-icon-bar-bg:        transparent;
$navbar-default-toggle-border-color:       transparent;

//== Tooltips
$tooltip-arrow-width: 8px;

//== Popovers
$popover-max-width:                     220px;
$popover-title-bg:                      transparent;
$popover-border-color:                  transparent;
$popover-border-fallback-color:         transparent;
$popover-arrow-outer-color:             rgba(187, 187, 187, 0.4);
$popover-arrow-outer-fallback-color:    #ddd;
$popover-arrow-width:                   15px;

//== Modals
$modal-backdrop-bg:             $colorGreyDark;
$modal-title-line-height:       1.2;
$modal-lg:                      904px;
$modal-md:                      702px;
$modal-sm:                      300px;

//== Breadcrumbs
$breadcrumb-color:              $colorGrey;
$breadcrumb-active-color:       $colorGreyDark;

//== Close
$close-color:       $colorSecondary;

//== Panels
$panel-bg:                    transparent;
$panel-body-padding:          $spacing-base;
$panel-border-radius:         6px;
$panel-footer-padding:        15px;

//** Border color for elements within panels
$panel-inner-border:          #ddd !default;
$panel-footer-bg:             transparent;

$panel-default-text:          $gray-dark !default;
$panel-default-border:        $colorGrey;
$panel-default-heading-bg:    transparent;

$panel-primary-text:          #fff !default;
$panel-primary-border:        $brand-primary !default;
$panel-primary-heading-bg:    $brand-primary !default;

//== Alerts
//## Define alert colors, border radius, and padding.
$alert-padding:               $padding-large-horizontal;
$alert-link-font-weight:      inherit;

$alert-error-bg:             $colorAttention !default;
$alert-error-text:           $colorWhite !default;
$alert-error-border:         $colorAttention !default;

//== Wells
//
//##

$well-bg:                     $swatchGreyLight;
$well-border:                 0;

//== Pagination
$pagination-active-bg:        $colorSecondary;
$pagination-active-border:    $colorSecondary;

//== Thumbnails
//** Padding around the thumbnail image
$thumbnail-padding:           10px;
//** Thumbnail background color
$thumbnail-bg:                transparent;
//** Thumbnail border color
$thumbnail-border:            transparent;
//** Thumbnail border radius
$thumbnail-border-radius:     0;

//** Custom text color for thumbnail captions
$thumbnail-caption-color:     $text-color !default;
//** Padding around the thumbnail caption
$thumbnail-caption-padding:   9px !default;

//== List group
//** Background color on `.list-group-item`
$list-group-border:     #ddd !default;

$transition-duration:   .1s;
$transition-timing:     ease-in-out;

$dropdown-with: 244px;

$block-list-border:    $list-group-border;


// ** Bootstrap 4 Variables

// Grid breakpoints
//
// Define the minimum dimensions at which your layout will change,
// adapting to different screen sizes, for use in media queries.

$grid-breakpoints: (
  xs: 0,
  sm: 768px,
  md: 992px,
  lg: 1200px
) !default;


// Spacing
//
// Control the default styling of most Bootstrap elements by modifying these
// variables. Mostly focused on spacing.
// You can add more entries to the $spacers map, should you need more variation.

// stylelint-disable
$spacer: 1rem !default;
$spacers: () !default;
$spacers: map-merge((
  0: 0,
  1: ($spacer * .25),
  2: ($spacer * .5),
  3: $spacer,
  4: ($spacer * 1.5),
  5: ($spacer * 3)
), $spacers);


// Utilities

$displays: none, inline, inline-block, block, table, table-row, table-cell, flex, inline-flex !default;


// Livechat
//
// Livechat
$livechat-bg:                 $colorWhite;
$livechat-border-radius:      $border-radius-large;
$livechat-margin:             20px;
$livechat-max-width:          395px;
$livechat-max-height:         600px;

$livechat-header-padding-y:   4px;
$livechat-header-padding-x:   16px;


// Process
//
// Process Overview
$process-font-size:               16px; // Needed for the em sizes to work, since root is set to 10px
$process-bg:                      $colorWhite;
$process-alt-bg:                  #f9f6f6;

$process-line-color:              $colorSecondary;
$process-line-width:              .5em;
$process-line-container-width:    3.5em;

$process-index-number-font-size:  1.5em;

$process-card-bg:                 $colorWhite;
$process-card-secondary-bg:       $colorSecondary;
$process-card-border-color:       rgba(0, 0, 0, .2);
$process-card-border-radius:      $border-radius-large;
$process-card-border-width:       1px;
$process-card-margin:             1em;
$process-card-padding:            1em;

$process-card-arrow-top:          1em;
$process-card-arrow-width:        1em;
$process-card-arrow-height:       2em;
$process-card-arrow-margin:       .25em;
$process-card-arrow-color:        $process-card-bg;
$process-card-arrow-outer-color:  rgba(0, 0, 0, .25);

$process-circle-color:            $colorWhite;
$process-circle-bg:               $colorSecondary;
$process-circle-size:             $process-card-arrow-height + ($process-card-arrow-margin * 2);
$process-circle-start-size:       $process-line-container-width;
$process-circle-last-size:        $process-circle-size + .5em;
